import Spline from '@splinetool/react-spline';
import './App.css'
import { useEffect, useState } from 'react';
import Scroll from './Scroll';

export default function App() {
  const [state, setState] = useState(false)
  useEffect(()=>{
    setInterval(()=>{
      setState(true)
    },6000)
  },[])
  return (
   <>
   { state ? <h1 style={{position: 'absolute', top: '300px', left: '30vw', zIndex: '56', color: 'white'}}>Welcome To Our website</h1> : <h1></h1>}
    <Spline
    
    style={{width:'100vw',height: '100vh', position: 'fixed', top:'0'}}
    scene="https://prod.spline.design/uGmGtWJP9v9U1w5z/scene.splinecode" />
    {state?<Scroll /> : ""}
   </>
  );
}
